:root {
  --background-primary: black;
  --background-secondary: darkgray;
  --background-accent: red;
  --background-red-primary: #311210;
  --foreground-primary: grey;
  --foreground-secondary: black;
  --foreground-accent: white;
  --text-primary: white;
  --text-secondary: #46f;
  --text-accent: #a19969;
  --link-text-primary: white;
  --link-text-secondary: black;
  --link-text-active-primary: red;
  --link-text-active-secondary: #820000;
  --card-background: #311210;
  --card-foreground: white;
  --toggle-selected: #46f;
  --spacing-large: 24px;
  --spacing-medium: 12px;
  --spacing-tiny: 8px;
  --spacing-xtiny: 4px;
}

body {
  background-color: var(--background-primary);
  height: 100vh;
  color: var(--text-primary);
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

main {
  max-width: 650px;
  padding-left: var(--spacing-medium);
  padding-right: var(--spacing-medium);
  flex-grow: 1;
  align-self: center;
}

button {
  all: unset;
  background-color: var(--background-secondary);
  padding: var(--spacing-tiny);
  color: var(--foreground-secondary);
  border-radius: 4px;
}

button.active {
  background-color: var(--link-text-active-secondary);
  color: #d3d3d3;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h4 {
  margin-bottom: 0;
}

header {
  padding-bottom: 16px;
}

header a {
  color: var(--text-accent);
}

header h1 {
  margin-bottom: 0;
}

a {
  color: var(--link-text-active-primary);
}

#references {
  text-align: center;
}

.runeword-actions {
  margin-top: var(--spacing-medium);
  margin-right: auto;
  margin-bottom: var(--spacing-medium);
  gap: var(--spacing-xtiny);
  flex-wrap: wrap;
  margin-left: auto;
  display: flex;
}

.header-block h3, .header-block h4 {
  margin-top: 0;
  margin-bottom: 0;
}

.header-block h3 {
  color: var(--text-accent);
}

.header-block {
  background-color: var(--card-background);
  color: var(--card-foreground);
  padding: var(--spacing-tiny);
  cursor: pointer;
  border: 1px outset var(--link-text-active-secondary);
  border-radius: 4px;
}

.flex-apart {
  justify-content: space-between;
  display: flex;
}

#runewords > ul, #runes > ul {
  min-width: 350px;
  max-width: 650px;
  margin-top: 1.33em;
  margin-left: auto;
  margin-right: auto;
}

#runewords > ul > li {
  margin-bottom: var(--spacing-xtiny);
}

#runewords > div:nth-child(4) > div {
  margin-top: 2em;
}

.runeword-effects {
  text-align: center;
  border: 1px solid var(--background-secondary);
  padding: var(--spacing-xtiny);
  color: var(--text-secondary);
  background-image: url("background.c4715a9f.png");
  background-size: cover;
  border-top: none;
}

header {
  margin-bottom: var(--spacing-medium);
}

footer, header {
  text-align: center;
  background-color: var(--background-red-primary);
  color: var(--text-accent);
}

footer {
  padding: var(--spacing-medium);
  margin-top: var(--spacing-medium);
}

header nav ul {
  gap: var(--spacing-medium);
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

header nav ul li {
  padding: var(--spacing-medium) 0px var(--spacing-medium) var(--spacing-medium);
}

header nav ul li a {
  color: var(--link-text-secondary);
}

header nav ul li a.nav-active {
  color: var(--link-text-active-secondary);
}

.switch {
  cursor: pointer;
  align-items: center;
  width: 60px;
  height: 24px;
  display: flex;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.switch:before {
  content: "on";
  width: 12px;
  height: 12px;
  color: var(--foreground-accent);
  z-index: 1;
  position: absolute;
  top: 2px;
  left: 6px;
}

.slider {
  cursor: pointer;
  background-color: var(--foreground-primary);
  border-radius: 4px;
  width: 100%;
  height: 100%;
  transition: all .4s;
  position: absolute;
}

.slider:before {
  cursor: pointer;
  content: "";
  background-color: #fff;
  width: 26px;
  height: 16px;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:checked + .slider {
  background-color: var(--toggle-selected);
}

input:focus + .slider {
  box-shadow: 0 0 8px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.switch-text {
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 96px;
  margin-left: 8px;
  margin-right: 8px;
  font-size: 12px;
  overflow: hidden;
}

.switch-container {
  align-items: center;
  display: flex;
}

.runeword-toggles {
  justify-content: center;
  gap: var(--spacing-xtiny);
  flex-flow: column wrap;
  width: fit-content;
  margin: auto;
  display: flex;
}
